@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}

p {
    color: v.$negro;
    font-size: 2rem;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
    height: auto;
}

/* --------------- */
h1, h2, h3 {
    margin: 0 0 v.$separacion  0 ;
    color: v.$negro;
}
h1 {
    font-size: 5rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3.2rem;
}
/*--------*/
ul{
    padding: 0;
}
li{
    padding: 0;
    list-style: none;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
