@charset "UTF-8";
.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #f46669;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #464646;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

/* --------------- */
h1, h2, h3 {
  margin: 0 0 3rem 0;
  color: #464646;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

/*--------*/
ul {
  padding: 0;
}

li {
  padding: 0;
  list-style: none;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 2rem;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    justify-content: start;
  }
}
.header__content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.header__logo {
  margin: 0;
  font-size: 4.2rem;
}
.header__login {
  border: 1px solid #f46669;
  font-weight: 500;
  letter-spacing: 1px;
  color: #f46669;
  background-color: transparent;
  transition: all 0.2s ease;
  cursor: pointer;
  padding-inline: 2rem;
  padding-block: 1rem;
}
@media (min-width: 768px) {
  .header__login {
    position: absolute;
    right: 0;
    padding: 1rem 4rem;
  }
}
@media (min-width: 1024px) {
  .header__login {
    padding: 1.2rem 6rem;
  }
}
.header__login:hover {
  color: #FFFFFF;
  background-color: #f46669;
}

.hero {
  background-image: url(../img/pi単ata.jpg);
  background-image: linear-gradient(to bottom, rgba(70, 70, 70, 0) 0%, rgba(70, 70, 70, 0.7) 100%), url(../img/pi単ata.jpg);
  height: 0;
  padding-bottom: 75%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}
@media (min-width: 768px) {
  .hero {
    padding-bottom: 46%;
  }
}
@media (min-width: 1024px) {
  .hero {
    padding-bottom: 25%;
  }
}
.hero__content {
  color: #FFFFFF;
  position: absolute;
  bottom: 0;
  padding-bottom: 4.5rem;
}
.hero__titulo {
  font-size: 8vw;
  margin-bottom: 2rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .hero__titulo {
    font-size: 5.2rem;
  }
}
.hero__texto {
  color: #FFFFFF;
  margin: 0 0 3rem 0;
  font-size: 4vw;
}
@media (min-width: 768px) {
  .hero__texto {
    font-size: 2rem;
  }
}
.hero__enlace {
  border: 1px solid #f46669;
  font-weight: 500;
  letter-spacing: 1px;
  color: #FFFFFF;
  background-color: #f46669;
  transition: all 0.2s ease;
  cursor: pointer;
  font-size: 4vw;
  padding: 2.5vw 4vw;
}
@media (min-width: 768px) {
  .hero__enlace {
    padding: 1.2rem 1.6rem;
    font-size: 1.6rem;
  }
}

.bloques {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .bloques {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}

.bloque {
  min-height: 26rem;
  background-position: center;
  background-size: cover;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .bloque {
    min-height: 40rem;
  }
}
.bloque:nth-child(1) {
  background-color: #f46669;
}
.bloque:nth-child(2) {
  background-image: url(../img/bloque_1.jpg);
  background-position: right;
}
.bloque:nth-child(3) {
  background-color: #c7c57d;
}
@media (min-width: 768px) {
  .bloque:nth-child(3) {
    grid-column: 2/3;
  }
}
.bloque:nth-child(4) {
  background-image: url(../img/bloque_2.jpg);
}
@media (min-width: 768px) {
  .bloque:nth-child(4) {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
.bloque__contenido {
  color: #FFFFFF;
  max-width: 40rem;
}
.bloque__contenido::before, .bloque__contenido::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #FFFFFF;
  margin: 3rem 0;
}
.bloque__heading {
  text-transform: uppercase;
  color: inherit;
}
.bloque__texto {
  color: inherit;
  font-size: 4vw;
}
@media (min-width: 768px) {
  .bloque__texto {
    font-size: 2rem;
  }
}

.productos {
  padding-block: 4rem;
}

.seccion {
  padding-block: 3rem;
}
.seccion__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 1.6rem;
}
.seccion__titulo {
  margin-bottom: 0;
}
.seccion__link {
  color: #f46669;
  font-weight: 700;
}
.seccion__productos {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .seccion__productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.seccion__img {
  width: 100%;
}
.seccion__nombre {
  font-size: 1.5rem;
  margin: 0.8rem 0;
}
.seccion__precio {
  margin: 0 0 0.8rem 0;
  font-weight: 700;
}
.seccion__linkProducto {
  color: #f46669;
  font-weight: 700;
}

.footer {
  background-image: url(../img/bloque_2.jpg);
  background-image: linear-gradient(to bottom, rgba(70, 70, 70, 0) 0%, rgba(70, 70, 70, 0.4) 100%), url(../img/bloque_2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  padding-block: 6rem;
}
.footer__contenido {
  display: grid;
}
@media (min-width: 768px) {
  .footer__contenido {
    grid-template-columns: 1fr 2fr;
  }
}
.footer__logo h1 {
  color: #FFFFFF;
  display: inline-block;
}
.footer__img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__img {
    margin: unset;
  }
}

.navegacion {
  margin: 0.5rem 0 4rem 0;
}
@media (min-width: 768px) {
  .navegacion {
    margin-bottom: 0;
  }
}
.navegacion__list {
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion__list {
    height: 100%;
    align-items: unset;
    align-self: stretch;
  }
}
.navegacion__enlace {
  color: #464646;
}

@media (min-width: 768px) {
  .formulario {
    grid-column: 2/3;
    grid-row: 1/3;
  }
}
@media (min-width: 1024px) {
  .formulario {
    grid-column: unset;
    grid-row: unset;
  }
}
.formulario__titulo {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #FFFFFF;
}
.formulario__campo {
  background-color: #FFFFFF;
  padding: 0.8rem 1.2rem;
  margin-bottom: 3rem;
  border-radius: 5px;
  transition: all 0.3s ease;
}
.formulario__campo:hover {
  box-shadow: 0px 2px 15px -5px rgba(0, 0, 0, 0.15);
  transform: translateY(-1px);
}
.formulario__label {
  color: #464646;
  font-size: 1.2rem;
}
.formulario__input, .formulario__select {
  background-color: transparent;
  width: 100%;
  border: none;
  margin-top: 0.5rem;
}
.formulario__input:focus, .formulario__select:focus {
  outline: none;
}
.formulario__textArea {
  border-radius: 5px;
  width: 100%;
  border: none;
  margin-bottom: 1rem;
  padding: 1.2rem;
  resize: none;
  font-size: 1.6rem;
  height: 10rem;
  transition: all 0.3s ease;
}
.formulario__textArea:focus {
  outline: none;
}
.formulario__textArea:hover {
  box-shadow: 0px 2px 15px -5px rgba(0, 0, 0, 0.15);
  transform: translateY(-1px);
}
.formulario__submit {
  border: 1px solid #f46669;
  font-weight: 500;
  letter-spacing: 1px;
  color: #FFFFFF;
  background-color: #f46669;
  transition: all 0.2s ease;
  cursor: pointer;
  padding: 1.5rem 2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: unset;
  }
}

.copyright {
  text-align: center;
  padding-block: 2rem;
}
.copyright__text {
  font-size: 1.6rem;
  line-height: 1.5;
  margin: 0;
}
.copyright__span {
  display: block;
}